import {
  FormGroup,
  FormControl,
  FormArray,
  ValidatorFn,
  AbstractControlOptions,
  AsyncValidatorFn
} from "@angular/forms";
import { Observable, Subject } from "rxjs";
// tslint:disable:max-classes-per-file

type TypeSafeControlType = FormControl | FormGroup | FormArray;
export type FormGroupControlsOf<T> = { [key in keyof T]: TypeSafeControlType };

export class FormGroupTypeSafe<ValueType> extends FormGroup {
  value: ValueType;
  controls: FormGroupControlsOf<Partial<ValueType>>;
  valueChanges: Observable<ValueType>;

  constructor(
    controls: FormGroupControlsOf<Partial<ValueType>>,
    validatorOrOpts?:
      | ValidatorFn
      | ValidatorFn[]
      | AbstractControlOptions
      | null,
    asyncValidator?: AsyncValidatorFn | AsyncValidatorFn[] | null
  ) {
    super(controls, validatorOrOpts, asyncValidator);
  }
}

export interface FormArrayControlChangedEvent<ValueType> {
  value: ValueType | Array<ValueType>;
  target: TypeSafeControlType | Array<TypeSafeControlType>;
  index?: number;
  type: FormArrayEventType;
}
export enum FormArrayEventType {
  Add = 0,
  Remove = 1,
  Updated = 2,
  Reset = 3,
  LastControlChanged = 4
}

export class FormArrayTypeSafe<
  ItemType extends TypeSafeControlType = any,
  ValueType = any
> extends FormArray {
  private onControlChangedSubject: Subject<
    FormArrayControlChangedEvent<ValueType>
  > = new Subject();

  value: Array<ValueType>;
  controls: ItemType[];
  valueChanges: Observable<Array<ValueType>>;
  onControlChanged = this.onControlChangedSubject.asObservable();

  constructor(
    controls: Array<ItemType>,
    validatorOrOpts?:
      | ValidatorFn
      | ValidatorFn[]
      | AbstractControlOptions
      | null,
    asyncValidator?: AsyncValidatorFn | AsyncValidatorFn[] | null
  ) {
    super(controls, validatorOrOpts, asyncValidator);
  }

  push(control: ItemType, isLast = true): void {
    super.push(control);
    this.onControlChangedSubject.next({
      index: this.length - 1,
      type: FormArrayEventType.Add,
      target: control,
      value: control.value
    });
  }
  insert(index: number, control: ItemType): void {
    super.insert(index, control);
    this.onControlChangedSubject.next({
      index,
      type: FormArrayEventType.Add,
      target: control,
      value: control.value
    });
  }
  removeAt(index: number): void {
    const control = this.controls[index];
    super.removeAt(index);
    this.onControlChangedSubject.next({
      index,
      type: FormArrayEventType.Remove,
      target: control,
      value: control.value
    });
  }
  setControl(index: number, control: ItemType): void {
    super.setControl(index, control);
    this.onControlChangedSubject.next({
      index,
      type: FormArrayEventType.Updated,
      target: control,
      value: control.value
    });
  }
  setValue(
    value: ValueType[],
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }
  ): void {
    super.setValue(value, options);
    this.onControlChangedSubject.next({
      type: FormArrayEventType.Updated,
      target: this.controls,
      value
    });
  }
  reset(
    value?: any,
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }
  ): void {
    super.reset(value, options);
    this.onControlChangedSubject.next({
      type: FormArrayEventType.Reset,
      target: this.controls,
      value
    });
  }
}
