import { Component, OnDestroy, Inject, PLATFORM_ID } from "@angular/core";
import { GlobalService } from "@root-shared/services/global.service";
import { filter, takeUntil } from "rxjs/operators";
import { Subject } from "rxjs";
import { isPlatformBrowser } from "@angular/common";

@Component({
  selector: "form-success-component",
  templateUrl: "./form-success.component.html"
})
export class FormSuccessComponent implements OnDestroy {
  protected readonly destroySubject: Subject<void> = new Subject<void>();
  isFormSuccess: boolean = false;
  isBrowser: boolean = false;

  constructor(
    private globalService: GlobalService,
    @Inject(PLATFORM_ID) private platformId: Object
  ) {
    if (isPlatformBrowser(this.platformId)) {
      this.isBrowser = true;
    }
    this.globalService.isFormSuccess
      .pipe(
        takeUntil(this.destroySubject),
        filter(x => x !== null)
      )
      .subscribe(response => {
        this.isFormSuccess = response;
        if (response) {
          setTimeout(() => {
            this.isFormSuccess = false;
          }, 3000);
        }
      });
  }
  hideMessage() {
    this.isFormSuccess = false;
  }

  ngOnDestroy() {
    this.destroySubject.next();
  }
}
