import {
  Component,
  ViewChild,
  AfterViewInit,
  AfterContentInit
} from "@angular/core";
import { PagesType } from "@root-shared/enums/pages.enum";
import { PagesLinksMap } from "@root-shared/enums/pageLinks.enum";
import { ActivatedRoute } from "@angular/router";
import { TabsetComponent } from "ngx-bootstrap";

@Component({
  selector: "about-page-component",
  templateUrl: "./about-page.component.html"
})
export class AboutPageComponent implements AfterContentInit {
  PagesType = PagesType;
  PagesLinksMap = PagesLinksMap;

  @ViewChild("staticTabs") staticTabs: TabsetComponent;

  constructor(private activatedRoute: ActivatedRoute) {}
  scrollTo(className: string): void {
    console.log(className);
    const elementList = document.querySelectorAll("." + className);
    const element = elementList[0] as HTMLElement;
    element.scrollIntoView({ behavior: "smooth" });
  }

  ngAfterContentInit(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      if (params.tabId) {
        this.staticTabs.tabs[params.tabId].active = true;
        this.scrollTo("js-scroll-tabs");
      }
    });
  }
}
