import { Component, OnInit } from "@angular/core";
import { PagesType } from "@root-shared/enums/pages.enum";
import { PagesLinksMap } from "@root-shared/enums/pageLinks.enum";
import * as AOS from "aos";
import * as Rellax from "rellax";
import { AboutTabs } from "@root-shared/enums/about-tabs.enum";
import { ExpertiseShortsItems } from "@root-shared/data/expertise-shorts";
import { IndustryShortsItems } from "@root-shared/data/industry-short";

@Component({
  selector: "home-page-component",
  templateUrl: "./home-page.component.html"
})
export class HomePageComponent implements OnInit {
  PagesType = PagesType;
  PagesLinksMap = PagesLinksMap;
  AboutTabs = AboutTabs;
  slides = ExpertiseShortsItems;
  IndustryShortsItems = IndustryShortsItems;
  constructor() {}

  slideConfig = {
    slidesToShow: 3,
    slidesToScroll: 1,
    dots: true,
    arrows: true,
    centerMode: true,
    variableWidth: true,
    nextArrow:
      '<span class="arrow arrow-right"><i class="icon-icon-chevron-right"></i></span>',
    prevArrow:
      '<span class="arrow arrow-left"><i class="icon-icon-chevron-left"></i></span>',
    responsive: [
      {
        breakpoint: 991,
        settings: {
          variableWidth: false,
          dots: false,
          arrows: false,
          autoplay: true,
          slidesToShow: 1
        }
      }
    ]
  };

  scrollTo() {
    window.scrollTo({
      top: window.innerHeight - 60,
      behavior: "smooth"
    });
  }

  ngOnInit() {
    AOS.init();
    const rellax = new Rellax(".rellax");
  }
}
