import { CommonModule } from "@angular/common";
import { NgModule, ModuleWithProviders } from "@angular/core";
import { ReactiveFormsModule, FormsModule } from "@angular/forms";
import { HttpClientModule } from "@angular/common/http";
import { RouterModule } from "@angular/router";
import { TooltipModule } from "ngx-bootstrap/tooltip";
import { BsDropdownModule } from "ngx-bootstrap/dropdown";
import { COMMON_COMPONENTS } from "./components";
import { NgSelectModule } from "@ng-select/ng-select";
import { LottieAnimationViewModule } from "ng-lottie";
import { AutoplayVideoDirective } from "./directives/video-autoplay.directive";
import { LinkService } from "./services";
import { LinkGuard } from "./guards";

@NgModule({
  declarations: [...COMMON_COMPONENTS, AutoplayVideoDirective],
  imports: [
    RouterModule,
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    TooltipModule.forRoot(),
    BsDropdownModule.forRoot(),
    HttpClientModule,
    NgSelectModule,
    LottieAnimationViewModule.forRoot()
  ],
  exports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    NgSelectModule,
    ...COMMON_COMPONENTS,
    AutoplayVideoDirective,
    TooltipModule,
    BsDropdownModule
  ],
  providers: []
})
export class RootSharedModule {
  public static forRoot(): ModuleWithProviders {
    return {
      ngModule: RootSharedModule,
      providers: [LinkService, LinkGuard]
    };
  }
}
