import {
  FormGroup,
  FormArray,
  AbstractControl,
  FormControl
} from "@angular/forms";
import { FormGroupTypeSafe } from "./forms-helper";

type MarkFormControlType =
  | FormGroup
  | FormArray
  | FormControl
  | FormGroupTypeSafe<any>;

export const markFormControl = {
  used: (control: MarkFormControlType, updateValueAndValidity = true) => {
    control.markAsDirty({ onlySelf: true });
    control.markAsTouched({ onlySelf: true });
    let childControls: AbstractControl[] = [];
    if (control instanceof FormArray) {
      childControls = control.controls;
    } else if (control instanceof FormGroup) {
      childControls = (Object as any).values(control.controls);
    } else if (control instanceof FormControl && updateValueAndValidity) {
      control.updateValueAndValidity();
    }
    childControls.forEach((childControl: MarkFormControlType) => {
      markFormControl.used(childControl);
    });
  },
  unused: (control: MarkFormControlType) => {
    control.markAsPristine({ onlySelf: true });
    control.markAsUntouched({ onlySelf: true });
    let childControls: AbstractControl[] = [];
    if (control instanceof FormArray) {
      childControls = control.controls;
    } else if (control instanceof FormGroup) {
      childControls = (Object as any).values(control.controls);
    }
    childControls.forEach((childControl: MarkFormControlType) => {
      markFormControl.unused(childControl);
    });
  }
};
