import { Injectable, Inject } from "@angular/core";
import { ActivatedRouteSnapshot, CanActivate, CanActivateChild, RouterStateSnapshot } from "@angular/router";

import { LinkService } from "@root-shared/services";
import { DOCUMENT } from "@angular/platform-browser";

@Injectable()
export class LinkGuard implements CanActivate, CanActivateChild {
  private activeLink: HTMLElement;
  constructor(private readonly linkService: LinkService, @Inject(DOCUMENT) private document) {}

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
    const metaSettings = route.hasOwnProperty("data") ? route.data.meta : undefined;

    if (this.activeLink) {
      this.activeLink.remove();
    }

    if (metaSettings && metaSettings.url) {
      this.activeLink = this.linkService.addTag({
        rel: "canonical",
        href: metaSettings.url
      });
    }

    return true;
  }

  canActivateChild(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
    return this.canActivate(route, state);
  }
}
