import { Component } from "@angular/core";
import { FormGroupTypeSafe } from "@root-shared/utils/forms-helper";
import { FormControl, Validators } from "@angular/forms";
import { IndustryTypeOptions } from "@root-shared/enums/industry-type.enum";
import { markFormControl } from "@root-shared/utils/mark-form-control";
import {
  ServicesTypeOptions,
  ServicesTypeMap
} from "@root-shared/enums/services.enum";
import { HttpClient } from "@angular/common/http";
import { GlobalService } from "@root-shared/services/global.service";
@Component({
  selector: "contacts-form-component",
  templateUrl: "./contacts-form.component.html"
})
export class ContactsFormComponent {
  IndustryTypeOptions = IndustryTypeOptions;
  ServicesTypeOptions = ServicesTypeOptions;
  ServicesTypeMap = ServicesTypeMap;

  formGroup = new FormGroupTypeSafe<ContactForm>({
    Name: new FormControl(null, Validators.required),
    Email: new FormControl(null, [Validators.email, Validators.required]),
    CompanyName: new FormControl(null),
    IndustryId: new FormControl(null),
    IndustryName: new FormControl(null),
    ServiceId: new FormControl(null),
    ServiceName: new FormControl(null),
    Subject: new FormControl("SLR"),
    Message: new FormControl(null)
  });

  get formControl() {
    return this.formGroup.controls;
  }

  constructor(private http: HttpClient, private globalService: GlobalService) {}

  onSaveForm() {
    markFormControl.used(this.formGroup);

    if (this.formGroup.valid) {
      this.formGroup.controls.ServiceName.reset(
        this.ServicesTypeMap.get(this.formGroup.controls.ServiceId.value)
      );

      this.http.post("/api/Mail", this.formGroup.value).subscribe(x => {
        if (x) {
          this.globalService.isFormSuccess.next(true);
          this.formGroup.reset();
          markFormControl.unused(this.formGroup);
        }
      });
    }
  }
}
