import { BrowserModule } from "@angular/platform-browser";
import { NgModule } from "@angular/core";

import { AppComponent } from "./app.component";
import { RouterModule } from "@angular/router";
import { HomePageComponent } from "./pages/home-page/home-page.component";
import { AboutPageComponent } from "./pages/about-page/about-page.component";
import { RootSharedModule } from "./root-shared/root-shared.module";
import { SlickCarouselModule } from "ngx-slick-carousel";
import { ExpertisePageComponent } from "./pages/expertise-page/expertise-page.component";
import { SHARED_PAGE_COMPONENTS } from "./pages/shared";
import { TabsModule } from "ngx-bootstrap/tabs";
import { IndustryPageComponent } from "./pages/industry-page/industry-page.component";
import { ModalModule } from "ngx-bootstrap/modal";
import { CareerPageComponent } from "./pages/career-page/career-page.component";
import { InfoCenterPageComponent } from "./pages/infocenter-page/infocenter-page.component";
import { ContactsPageComponent } from "./pages/contacts-page/contacts-page.component";
import { AgmCoreModule } from "@agm/core";
import { EXPERTISE_SINGLE_PAGES } from "./pages/expertise-page/components";
import { EXPERTISE_ROUTER_COMPONENT } from "./pages/expertise-page/components/router.component";
import { GlobalService } from "@root-shared/services/global.service";
import { INDUSTRY_COMPONENTS } from "./pages/industry-page/components";
import { INDUSTRY_ROUTER_COMPONENT } from "./pages/industry-page/components/router.component";
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { Angulartics2Module } from "angulartics2";
import { environment } from "environments/environment";
import { MetaModule, MetaGuard, MetaLoader, MetaStaticLoader, PageTitlePositioning } from "@ngx-meta/core";
import { LinkGuard } from "@root-shared/guards";

export function metaFactory(): MetaLoader {
	return new MetaStaticLoader({
		pageTitlePositioning: PageTitlePositioning.PrependPageTitle,
		defaults: {
			type: "article",
			site_name: "Smart Legal Resolution",
			title: "Юридическая консалтинговая группа SLR - Smart Legal Resolution",
			description:
				"Компания SLR предоставляет широкий спектр услуг для повышения эффективности деятельности компаний. Мы знаем ответы на многие вопросы, волнующие бизнес, и обладаем достаточной экспертизой в многих практиках.",
			url: "https://slr.legal",
			"og:image": "https://slr.legal/favicon.ico",
			"og:type": "article",
			"og:locale": "ru_RU"
		}
	});
}

@NgModule({
	declarations: [
		AppComponent,
		HomePageComponent,
		AboutPageComponent,
		ExpertisePageComponent,
		IndustryPageComponent,
		ContactsPageComponent,
		InfoCenterPageComponent,
		CareerPageComponent,
		...SHARED_PAGE_COMPONENTS,
		...EXPERTISE_SINGLE_PAGES,
		...INDUSTRY_COMPONENTS
	],
	imports: [
		BrowserModule.withServerTransition({ appId: "sls-app" }),
		BrowserAnimationsModule,
		MetaModule.forRoot({
			provide: MetaLoader,
			useFactory: metaFactory
		}),
		RootSharedModule.forRoot(),
		SlickCarouselModule,
		TabsModule.forRoot(),
		ModalModule.forRoot(),
		AgmCoreModule.forRoot({
			apiKey: "AIzaSyAbdH022tswymNTzj-uiDldEMBhmb3dsl0"
		}),
		Angulartics2Module.forRoot({
			gtm: { userId: "GTM-KDSF8KH" },
			ga: { userId: "UA-136204572-1" }
			//developerMode: !environment.production
		}),
		RouterModule.forRoot(
			[
				{
					path: "",
					canActivateChild: [MetaGuard, LinkGuard],
					children: [
						{
							path: "",
							data: {
								animatedLogo: true,
								meta: {
									title: "Юридическая консалтинговая группа SLR - Smart Legal Resolution",
									description:
										"Компания SLR предоставляет широкий спектр услуг для повышения эффективности деятельности компаний. Мы знаем ответы на многие вопросы, волнующие бизнес, и обладаем достаточной экспертизой в многих практиках.",
									url: "https://slr.legal"
								}
							},
							component: HomePageComponent
						},
						{
							path: "about",
							data: {
								meta: {
									title: "О компании SLR - Smart Legal Resolution",
									description:
										"Мы не хуже и не лучше других, мы отличаемся от всех, мы – иные. Наша компания пропитана духом новаторства и авантюризма, работает в стиле асимметрии к действиям основных игроков, секторов и рынков. У нас другое видение, мы создаем новые бизнес-модели, ломаем правила и переворачиваем шахматные доски",
									url: "https://slr.legal/about"
								}
							},
							component: AboutPageComponent
						},

						{
							path: "expertise",
							data: {
								meta: {
									title: "Экспертизы и практики компании SLR - Smart Legal Resolution",
									description:
										"Компания SLR является профессионалами во многих юридических экспертизах и практиках, что позволяет эффективно и качественно решать задачи любого бизнеса в короткие сроки.",
									url: "https://slr.legal/expertise"
								}
							},
							component: ExpertisePageComponent
						},

						{
							path: "career",
							data: {
								meta: {
									title: "Карьера с SLR - Smart Legal Resolution",
									description:
										"Приглашаем к сотрудничеству людей, которые готовы к интересной и креативной работе. Если ты талантлив и эффективен, то ты тот кто нам нужен. Гарантируем возможности и перспективы интересной работы.",
									url: "https://slr.legal/career"
								}
							},
							component: CareerPageComponent
						},

						{
							path: "infocenter",
							data: {
								meta: {
									url: "https://slr.legal/infocenter"
								}
							},
							component: InfoCenterPageComponent
						},

						{
							path: "contacts",
							data: {
								hideMobBigLogo: true,
								meta: {
									title: "Контакты SLR - Smart Legal Resolution",
									description:
										"Контактные данные компании Smart Legal Resolution. Напиши нам info@slr.legal Телефон +38 098 8899 008",
									url: "https://slr.legal/contacts"
								}
							},
							component: ContactsPageComponent
						},

						{
							path: "industry",
							data: {
								meta: {
									title: "Индустрии SLR - Smart Legal Resolution",
									description:
										"Компания SLR обладает значительным опытом в консультировании большого количества индустрий бизнеса. Основными являются IT-компании, Call-центры, современное исскуство, а также многое другое",
									url: "https://slr.legal/industry"
								}
							},
							component: IndustryPageComponent
						},
						...EXPERTISE_ROUTER_COMPONENT,
						...INDUSTRY_ROUTER_COMPONENT
					]
				},
				{
					path: "**",
					redirectTo: "/"
				}
			],
			{
				scrollPositionRestoration: "enabled",
				initialNavigation: "enabled"
			}
		)
	],
	providers: [GlobalService],
	bootstrap: [AppComponent]
})
export class AppModule {}
