import {
  Component,
  HostListener,
  OnInit,
  OnDestroy,
  Renderer
} from "@angular/core";
import { PagesType } from "@root-shared/enums/pages.enum";
import { PagesLinksMap } from "@root-shared/enums/pageLinks.enum";
import * as bodymovin from "lottie-web";
import { trigger, style, state } from "@angular/animations";
import { Subject } from "rxjs";
import {
  ActivatedRoute,
  Router,
  NavigationEnd,
  ActivatedRouteSnapshot
} from "@angular/router";
import { filter, map, takeUntil } from "rxjs/operators";
@Component({
  selector: "header-component",
  templateUrl: "./header.component.html",
  styles: [
    `
      .overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #000;
        z-index: 201;
        opacity: 1;
        transition: all 1s;
      }
      .overlay.-hide {
        opacity: 0;
        pointer-events: none;
      }
    `
  ],
  animations: [
    trigger("moveToHeader", [
      state(
        "move",
        style({
          top: "30px",
          left: "50px",
          transform: "translate(0%, 0%)",
          maxWidth: "250px"
        })
      ),
      state(
        "moveMobile",
        style({
          top: "70px",
          transform: "translate(-50%, 0%)",
          maxWidth: "250px"
        })
      )
    ])
  ]
})
export class HeaderComponent implements OnInit, OnDestroy {
  protected readonly destroySubject: Subject<void> = new Subject<void>();

  isScrollDown: boolean = false;
  isHeaderBlack: boolean = false;
  isHomePage: boolean = false;
  isShowPreloader: boolean = false;
  isAlwaysSmall: boolean = false;
  headerAnimation;
  isAnimationEnded: boolean = false;
  animateTo: string = "";
  PagesType = PagesType;
  PagesLinksMap = PagesLinksMap;
  animationComplete: boolean = false;

  isOpenMobileMenu: boolean = false;
  hideBigMobLogo: boolean = false;

  @HostListener("window:scroll", ["$event"]) onWindowScroll($event) {
    const pageYOffset = window.pageYOffset;
    if (pageYOffset > 50) {
      this.isScrollDown = true;
    } else {
      this.isScrollDown = false;
    }
  }
  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private renderer: Renderer
  ) {
    this.router.events
      .pipe(
        takeUntil(this.destroySubject),
        filter(event => event instanceof NavigationEnd),
        map(() => this.activatedRoute.snapshot),
        map(route => {
          while (route.firstChild) {
            route = route.firstChild;
          }
          return route;
        })
      )
      .subscribe((route: ActivatedRouteSnapshot) => {
        this.isOpenMobileMenu = false;
        if (route.data.headerBlackOverlay) {
          this.isHeaderBlack = true;
          this.isAlwaysSmall = true;
        } else {
          this.isHeaderBlack = false;
          this.isAlwaysSmall = false;
        }
        if (route.data.animatedLogo) {
          this.isShowPreloader = true;
          this.headerAnimation.playSpeed = 1;
        } else {
          this.isShowPreloader = false;
          this.headerAnimation.playSpeed = 100;
        }

        if (window.innerWidth < 1200) {
          if (route.data.hideMobBigLogo) {
            this.hideBigMobLogo = true;
          } else {
            this.hideBigMobLogo = false;
          }
        }
      });
  }
  ngOnInit() {
    this.setBodyDisabled(true);
    this.headerAnimation = bodymovin.loadAnimation({
      container: document.getElementById("js-logo"),
      renderer: "svg",
      loop: false,
      autoplay: true,
      path: "./assets/data/data.json"
    });
    this.headerAnimation.onComplete = () => {
      const windowWidth = window.innerWidth;
      if (windowWidth > 1200) {
        this.animateTo = "move";
      } else {
        this.animateTo = "moveMobile";
      }

      this.animationComplete = true;
      this.setBodyDisabled(false);
    };
  }
  moveToHeaderDone(e) {
    if (e.toState === "move" || e.toState === "moveMobile") {
      this.isAnimationEnded = true;
    }
  }
  manageMobileMenu() {
    this.isOpenMobileMenu = !this.isOpenMobileMenu;
    if (this.isOpenMobileMenu) {
      this.setBodyDisabled(true);
    } else {
      this.setBodyDisabled(false);
    }
  }

  setBodyDisabled(condition: boolean) {
    if (condition) {
      this.renderer.setElementClass(document.body, "-disabled", true);
    } else {
      this.renderer.setElementClass(document.body, "-disabled", false);
    }
  }
  ngOnDestroy(): void {
    this.destroySubject.next();
  }
}
